
.formControl.error input,
.formControl.error textarea {
    border: 2px solid blue;
}

.formControl.error span{
    color:blue
}
.formSummeryError {
    border: 2px solid blue;
    padding: 5px;
    color:blue;
}
.edit {
    margin-top: 10px;
    margin-bottom: 10px;
    display:flex;
    justify-content: center;
}
.edit button {
    width: 70px;
    text-transform: uppercase;
    height: 30px;
    color: black;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    outline: none;
    user-select: none;
}
.edit button:hover{
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4);
    color: white;
    transform: translateY(-7px);
}