.paginator {
display: flex;
    justify-content: center;

    margin-bottom: 20px;

}
.paginator button {
    width: 70px;
    text-transform: uppercase;
    height: 30px;
    color: black;
    font-size: 15px;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;

    outline: none;
}

.pageNumber {
    padding: 5px;
    border: 1px solid grey;
    background-color: inherit;
    border-radius: 5px;
}

.selectedPage {
    padding: 5px;
    font-weight: bold;
    cursor: pointer;
    border: 2px solid black;
    border-radius: 5px;
    background-color: lightgrey;

}

span:hover {
    background-color: #00aced;
}

span.pageNumber {
    margin: 3px;
    cursor: pointer;
}

.paginator button {
    cursor: pointer;
}