@import url('https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap');

.header {
    display: flex;
    width: 100vw;
    height: 70px;
    background-color: #4267b2;;
    font-family: 'Permanent Marker', cursive;
    position: fixed;
    justify-content: flex-end;
    z-index: 2;

}

.header img {
    z-index: 2;
    width: 50px;

}

.blockImage {
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    transform: translate(10px, 10px);
    transition: all 0.5s;
    z-index: 2;
    color:white;
}

.blockImage img {
    opacity: 1;
    transition: all 0.9s;
}

.blockImageDown {
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    padding: 10px;
    transform: translate(0, -50px);
    transition: all 0.5s;
    z-index: 2;
    text-shadow: 0 0 35px white;
    color: white;
}

.blockImageDown img {
    opacity: 0;
    transition: all 0.5s;
}

.blockImageDown p, h2 {
    margin: 0;

}

.blockImage h2 {
    z-index: 2;
    margin: 0;
    padding-top: 10px;

}

.blockImage p {
    z-index: 2;
    margin: 0;
}

.loginBlock {
    display: flex;
    width: 200px;
    position: absolute;
    align-items: center;
    align-self: center;
    padding-right: 30px;
}
.loginBlock img {
    margin-right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.loginBlock button {
    margin-left: 10px;
    border-radius:4px;
    border:none;
}
.loginBlock a {
    color : white;
    text-decoration: none;
}

.loginBlock button:hover {
    background-color: aqua;

}

.loginBlock div:nth-child(2){
    color:white;
}

.fixedCenter {
    position: absolute;
    width: 300px;
    height: 300px;
    transform: rotate(45deg);
    background-color: #4267b2;;
    left: 627px;
    bottom: -20px;
    transition: all 0.5s ease-out;
}

.scroll_down {
    left: 627px;
    bottom: -20px;
    position: absolute;
    width: 300px;
    height: 300px;
    transform: rotate(45deg) translate(-65px, -65px);
    background-color: #4267b2;;
    transition: all 0.5s ease-out;
}