.userPhoto {
    width: 80px;
    height: 80px;
}

.userPhotoBlock {
    width: 100px;
}

.usersContainer {
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: #e9ebee;

}

.userContainer {
    width: 800px;
    height: 100vw;
    margin: 100px 0 20px 0;
    padding-top: 30px;
    background-color: white;
}

.usersContainer div:nth-child(1) {
    order: 2
}

.userProfile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #e9ebee;
    margin: 20px;
}
.userProfile button {
    text-transform: uppercase;
    height: 30px;
    color: black;
    font-size: 10px;
    font-weight: bold;
    letter-spacing: 2px;
    text-shadow: 0 4px 3px rgba(0,0,0,0.4),
    0 8px 13px rgba(0,0,0,0.1),
    0 18px 23px rgba(0,0,0,0.1);
    border-radius: 45px;
    background-color: #00aced;
    box-shadow: 0 8px 15px rgba(0,0,0,.8);
    transition: .3s;
    border:none;
    margin-top: 20px;
    margin-right: 20px;
    cursor: pointer ;
}
.userProfile button:hover {
    background: #2EE59D;
    box-shadow: 0 15px 20px rgba(46,229,157,.4);
    color: white;
    transform: translateY(-7px);
}
.userPhotoBlock {
    margin-left: 20px;
    margin-bottom: 20px;
}

.userInfo {
    width:80px;

    padding-right: 450px;

}